body {
  width: 500px;
  text-align: center;
  margin: 20px auto;
  margin-top: 50px;
  border: 1px solid black;
}
body > div {
  margin: 30px auto;
}
h1 {
  font-size: 1.5em;
  font-weight: 300;
}
input {
  width: 70%;
  height: 2em;
  margin: 5px;
  border: none;
  padding-top: 2px;
  border-bottom: 1px solid black;
}
input:focus {
  outline: none;
  background-color: rgba(211, 211, 211, 30%);
}
form > button {
  outline: none;
  background-color: transparent;
  padding: 6px 10px;
}
button:focus {
  outline: none;
}
ol,
ul {
  padding: 10px 5px 10px 10px;
  width: 81%;
  margin: 0 auto;
}

li {
  border-radius: 5px;
  list-style: none;
  padding: 5px;
  text-align: left;
}
ul > li:hover {
  background-color: rgba(211, 211, 211, 30%);
}
li > span {
  margin-left: 25px;
  float: right;
  font-size: 0.8em;
  line-height: 2;
}
ul > li > div {
  border-radius: 0 0 5px 5px;
  min-height: 50px;
  background-color: rgba(211, 211, 211, 30%);
  padding: 5px;
  margin-top: 5px;
}
ul > li:hover > div {
  background-color: rgba(255, 255, 255, 80%);
}
.hidden {
  display: none;
}
